import React, { Component } from 'react';
import style from './../css/Header.css'

class Header extends Component {
	render() {
		return (
			<header>
				<div className="header__main header">
					<h1 className = 'header__title'><a className = 'header__link' href="/">TrailerGit!</a></h1>
				</div>
			</header>
		);
	}
}

export default Header