import React, { Component } from 'react';
import style from './../css/Genre.css';


class ChoiceGenre extends Component {
	render() {
		return(
			<div>
				<ul className = 'genre__list list'>
					{this.props.allGenre.map((item, index) =>
						<li className = 'list__el'><a className = 'list__link' href={'/filter/' + this.props.transliterate(item)}>{item}</a></li>
					)}
				</ul>
			</div>
		);
	}
}

export default ChoiceGenre;