import React, { Component } from 'react';
import { BrowserRouter } from 'react-router-dom';
import style from './../css/Trailer-view.css';

var	state = {
		active: true
	}

var showMore = function () {
	var btn = document.querySelectorAll('.desc__more')[0];
	var block = document.querySelectorAll('.desc__main')[0];

	if (state.active){
		block.style.height = '100%';
		btn.textContent = 'Свернуть...';
		state.active = false;
	} else {
		block.style.height = '13vw';
		btn.textContent = 'Побробнее...'
		state.active = true;
	}

}

class TrailerView extends Component {
	render() {

		var rolesText = 'В главных ролях';
		var genreText = 'Жанр';
		var countryText = 'Страна';

		if (this.props.roles.length === 0){
			rolesText = '';
		} else if (this.props.genre === 0){
			genreText = '';
		} else if (this.props.coutry === 0){
			countryText = '';
		}

		return(
			<BrowserRouter history = {this.props.history}>
				<div className = 'trailerView'>
					<img className = 'trailerView__img' src={require('./../img/' + this.props.image)} alt="image"/>
					<div className="trailerView__info info">
						<div className="info__name name info__el grey">
							<p className="name__title info__el__name">Название </p>
							<h2 className = 'name__text info__el__text'>{this.props.name}</h2>
						</div>
						<div className="info__year year info__el">
							<p className="year__title info__el__name">Год </p>
							<h2 className = 'year__text info__el__text'>{this.props.year}</h2>
						</div>
						<div className="info__coutry coutry info__el grey">
							<p className="coutry__title info__el__name">{countryText}</p>
							<h2 className = 'country__text info__el__text'>{this.props.country.map((num, index) =>
								<a key = {index} className = 'info__link' href = {'/filter/' + this.props.transliterate(num)}>{num + ' '}</a>
							)}
							</h2>
						</div>
						<div className="info__genre genre info__el">
							<p className="genre__title info__el__name">{genreText}</p>
							<h2 className = 'genre__text info__el__text'>{this.props.genre.map((num, index) =>	
								<a key = {index} className = 'info__link' href = {'/filter/' + this.props.transliterate(num)}>{num + ' '}</a>	
							)}
							</h2>
						</div>
						<div className="info__time time info__el grey">
							<p className="time__title info__el__name">Время </p>
							<h2 className = 'time__text info__el__text'>{this.props.time}</h2>
						</div>
						<div className="info__roles roles info__el">
							<p id = 'test' className="roles__title info__el__name">{rolesText}</p>
							<h2 className = 'roles__text info__el__text'>{this.props.roles.map((num, index) =>
								<a key = {index} className = 'info__link' href = {'/filter/' + this.props.transliterate(num)}>{num + ' '}</a>								
							)}
							</h2>
						</div>
					</div>
					<div className="trailerView__desc desc">
						<div className="desc__main">
							<p className="desc__text">{this.props.desc}</p>
						</div>
						<a onClick = {showMore} className = 'desc__more'>Побробнее...</a>
					</div>
				</div>
			</BrowserRouter>	
		);
	}
}

export default TrailerView