import React, { Component } from 'react';
import JwPagination from './pagination';
import TrailersIdea from './trailer-idea';
import ALL_TRAILERS from './../reducers/all-trailers'
import style from './../css/Home.css'



class Home extends Component {


  constructor() {
        super();

        // an example array of items to be paged
        var Items = ALL_TRAILERS;

        this.state = {
            exampleItems: Items,
            pageOfItems: []
        };

        // bind function in constructor instead of render (https://github.com/yannickcr/eslint-plugin-react/blob/master/docs/rules/jsx-no-bind.md)
        this.onChangePage = this.onChangePage.bind(this);
    }

    onChangePage(pageOfItems) {
        // update state with new page of items
        this.setState({ pageOfItems: pageOfItems });
    }

	render() {
		return(
	        <div className="main">
	            {this.state.pageOfItems.map((item, index) =>
	                <a className = 'linkToTrailer' href = {'/film/' + this.props.transliterate(item.name)}>
	                	<TrailersIdea year = {item.year} img = {item.img} name = {item.name}>
	                  	</TrailersIdea>
	               	</a>
	              )}
	            <JwPagination items={this.state.exampleItems} onChangePage={this.onChangePage} />
	        </div> 
		);
	}
}

export default Home