import React, { Component } from 'react';
import { BrowserRouter, Route } from 'react-router-dom';
import createBrowserHistory from 'history/createBrowserHistory';
import Home from './components/Home';
import Header from './components/Header';
import TrailerView from './components/trailer-view';
import ALL_TRAILERS from './reducers/all-trailers';
import TrailerSearch from './components/trailer-search';
import ChoiceGenre from './components/ChoiceGenre';
import FilteredHome from './components/filteredHome';
import './App.css';

var allGenre = [];
var allRoles = [];
var allCountry = [];

function addAllGenreToArray () {
  var numG = 0;
  var numR = 0;
  var numC = 0;
  ALL_TRAILERS.map((item, index) => {
    for (let i = 0; i < item.genre.length; i++){
      allGenre[numG] = item.genre[i]
      numG++
    }

    for (let i = 0; i < item.roles.length; i++){
      allRoles[numR] = item.roles[i]
      numR++
    }

    for (let i = 0; i < item.country.length; i++){
      allCountry[numC] = item.country[i]
      numC++
    }
  })
}

function uniqueGenre(arr) {
  var obj = {};

  for (var i = 0; i < allGenre.length; i++) {
    var str = allGenre[i];
    obj[str] = true;
  }

  return Object.keys(obj);
}

function uniqueRoles(arr) {
  var obj = {};

  for (var i = 0; i < allRoles.length; i++) {
    var str = allRoles[i];
    obj[str] = true;
  }

  return Object.keys(obj);
}

function uniqueCountry(arr) {
  var obj = {};

  for (var i = 0; i < allCountry.length; i++) {
    var str = allCountry[i];
    obj[str] = true;
  }

  return Object.keys(obj);
}

addAllGenreToArray();
allGenre = uniqueGenre(allGenre).sort();
allRoles = uniqueRoles(allRoles).sort();;
allCountry = uniqueCountry(allCountry).sort();

var transliterate = (
    function() {
        var
            rus = "щ   ш  ч  ц  ю  я  ё  ж  ъ  ы  э  а б в г д е з и й к л м н о п р с т у ф х ь".split(/ +/g),
            eng = "shh sh ch cz yu ya yo zh `` y' e` a b v g d e z i j k l m n o p r s t u f x `".split(/ +/g)
        ;
        return function(text, engToRus) {
            var x;
            for(x = 0; x < rus.length; x++) {
                text = text.split(engToRus ? eng[x] : rus[x]).join(engToRus ? rus[x] : eng[x]);
                text = text.split(engToRus ? eng[x].toUpperCase() : rus[x].toUpperCase()).join(engToRus ? rus[x].toUpperCase() : eng[x].toUpperCase());
            }
            return text;
        }
    }
)();

const history = createBrowserHistory();

class App extends Component {
  render() {
    return (
      <div className = 'app'>
        <div className="header">
          <Header></Header>
          <TrailerSearch transliterate = {transliterate} className = 'header__search'></TrailerSearch>
          <ChoiceGenre transliterate = {transliterate} allGenre = {allGenre} className = 'genre'></ChoiceGenre>
        </div>
        <BrowserRouter history = {history}>
        <Route exact path = '/' render = {() => <Home transliterate = {transliterate}></Home>}></Route>
          {ALL_TRAILERS.map((item, index) =>
            <Route path = {'/film/' + transliterate(item.name)} 
              render = {() => <TrailerView
              year = {item.year} 
              image = {item.img} 
              name = {item.name}
              country = {item.country}
              genre = {item.genre}
              time = {item.time}
              roles = {item.roles}
              desc = {item.desc}
              transliterate = {transliterate}
              history = {history}
              ></TrailerView>}>
            </Route>
          )}
          {allGenre.map((item, index) =>
            <Route path = {'/filter/' + transliterate(item)} 
              render = {() => <FilteredHome
                genre = {item}
                transliterate = {transliterate} ></FilteredHome>}>
            </Route>
            )}
          {allRoles.map((item, index) =>
            <Route path = {'/filter/' + transliterate(item)} 
              render = {() => <FilteredHome
              roles = {item}
              transliterate = {transliterate}  ></FilteredHome>}>
            </Route>
            )}
          {allCountry.map((item, index) =>
            <Route path = {'/filter/' + transliterate(item)} 
              render = {() => <FilteredHome
              country = {item}
              transliterate = {transliterate}  ></FilteredHome>}>
            </Route>
            )}
        </BrowserRouter>
      </div>
    );
  }
}


export default App;
