import React, { Component } from 'react';
import ALL_TRAILERS from './../reducers/all-trailers';
import style from './../css/TrailerSearch.css';

var transliterate = (
    function() {
        var
            rus = "щ   ш  ч  ц  ю  я  ё  ж  ъ  ы  э  а б в г д е з и й к л м н о п р с т у ф х ь".split(/ +/g),
            eng = "shh sh ch cz yu ya yo zh `` y' e` a b v g d e z i j k l m n o p r s t u f x `".split(/ +/g)
        ;
        return function(text, engToRus) {
            var x;
            for(x = 0; x < rus.length; x++) {
                text = text.split(engToRus ? eng[x] : rus[x]).join(engToRus ? rus[x] : eng[x]);
                text = text.split(engToRus ? eng[x].toUpperCase() : rus[x].toUpperCase()).join(engToRus ? rus[x].toUpperCase() : eng[x].toUpperCase());
            }
            return text;
        }
    }
)();

function setLocation(curLoc){
  global.location.href = curLoc;
  global.location.hash = curLoc;
}

function searchTrailer () {
	var trailerName = document.getElementById('trailerName');

	ALL_TRAILERS.map((item, index) => {
		if (item.name === trailerName.value){
			setLocation('/film/' + transliterate(item.name));			
		}
		else return;
	})
}

class TrailerSearch extends Component {
	handleKeyPress = (e) => {
	  if (e.charCode === 13) {
        setLocation('/film/' + transliterate(document.getElementById('trailerName').value));
      }
      if (e.keyCode === 13) {
        setLocation('/film/' + transliterate(document.getElementById('trailerName').value));
      }      
	}

	render () {
		return (
			<div className="trailerSearch tsearch">
				<form action="#" className = 'tsearch__form form'>
					<input onKeyPress = {this.handleKeyPress} id = 'trailerName' className = 'form__name' type="text" placeholder = 'Название фильма...'/>
					<input onClick = {searchTrailer} className = 'form__btn' type="button" value = 'Найти'/>
				</form>
			</div>
		);
	}
}

export default TrailerSearch