import React, { Component } from 'react';

class Trailer extends Component {
	render() {
		return(
			<div>
				<div className="trailerIdea">
					<img src={require('./../img/' + this.props.img)} alt={this.props.name + ' img'} className="trailerIdea__img"/>
					<div className="trailerIdea__info">
						<h4 className = 'trailerIdea__title'>{this.props.name}</h4>
						<p className="trailerIdea__year">{this.props.year}</p>
					</div>
				</div>
			</div>
		);
	}
}

export default Trailer