import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import ALL_TRAILERS from './../reducers/all-trailers'
import TrailersIdea from './trailer-idea';
import style from './../css/Home.css';

class FilteredHome extends Component {
	render() {
		return(
			<div className = 'filtered'>
				{ALL_TRAILERS.map((item, index) =>
                    {
                        if (item.genre.indexOf(this.props.genre) !== -1){
                            return (
                                <Link className = 'linkToTrailer' to = {'/film/' + this.props.transliterate(item.name)}>
                                    <TrailersIdea year = {item.year} img = {item.img} name = {item.name}>
                                    </TrailersIdea>
                                </Link> 
                            )
                        } else if (item.roles.indexOf(this.props.roles) !== -1) {
                            return (
                                <Link className = 'linkToTrailer' to = {'/film/' + this.props.transliterate(item.name)}>
                                    <TrailersIdea year = {item.year} img = {item.img} name = {item.name}>
                                    </TrailersIdea>
                                </Link> 
                            )
                        } else if (item.country.indexOf(this.props.country) !== -1) {
                            return (
                                <Link className = 'linkToTrailer' to = {'/film/' + this.props.transliterate(item.name)}>
                                    <TrailersIdea year = {item.year} img = {item.img} name = {item.name}>
                                    </TrailersIdea>
                                </Link> 
                            )
                        }
                    }
                )}
			</div>
		);
	}
}

export default FilteredHome;